<template>
  <div>
    <p class="p-2 rounded-lg shadow-lg" v-for="(item, index) in datanya" :key="index">
      {{item.gambar.indexOf('upload.egovteam')!=-1?'btn-danger':'btn-success'}}
      {{item.gambar}} <button type="button" @click="ubah(item)" :class="item.gambar.indexOf('upload.egovteam')!=-1?'btn-danger':'btn-success'" class="btn btn-sm  float-right">Ubah <span class="typcn typcn-edit"></span></button>
    </p>
    <button type="button" @click="test" class="btn btn-sm btn-primary  ">test</button>
  </div>
</template>
<script>
import axios from 'axios'
import firebase from 'firebase'
let db = firebase.firestore()
// let rdb = firebase.database()
import autophp from '@/plugins/autophp';
let sdb = new autophp();
export default{
  data(){
    return{
      datanya:[]
    }
  },
  methods:{
    test(){
      db.collection(`food`).onSnapshot(res=>{
          let data = res.docs.map(doc=>{
            return {
              id:doc.id,
              ...doc.data()
            }
          })
          console.log(data)
          this.datanya=data
      })
    },
    ubah(item){
      let gambarnya = '';
      gambarnya = item.gambar.replace('upload.egovteam','upload.egov')
      console.log(gambarnya)
      db.collection('food').doc(item.id).set({
        gambar:gambarnya
      },{merge:true}).then(res=>{
        console.log('berhasil edit')
      })
    }
  },
  mounted() {
    this.test()
  },
  metaInfo() {
      return {
        title: "Tentang TA_Maliki / Taufik Akbar Maliki",
        meta: [{  hid: "description",  name: "description",  content: "Tentang Taufik akbar maliki / TA_Maliki",},{  property: "og:title",  content: "Tentang Taufik akbar maliki / TA_Maliki",},{ property: "og:site_name", content: "TA_Maliki" },{ property: "og:type", content: "website" },{  property: `og:url`,  content: `${this.$store.state.url}`,},{  property: `og:image`,  content: `${this.$store.state.url}/Kino.jpg`,},{  property: `og:description`,  content: `documentasi ya`,},{  name: "keywords",  content: "blog,portofolio,freelance",},{ name: "robots", content: "index,follow" },],
      };
}}
</script>